// 576px, 768px, 992px, 1200px
$breakpoints-up: (
	'small': '36em',
	'medium': '48em',
	'large': '62em',
	'xlarge': '75em',
);

// 575px, 767px, 991px, 1199px
$breakpoints-down: (
	'small': '35.9375em',
	'medium': '47.9375em',
	'large': '61.9375em',
	'xlarge': '74.9375em',
);

@mixin breakpoint($size) {
	@media (min-width: map-get($breakpoints-up, $size)) {
		@content;
	}
}

@mixin breakpoint-down($size) {
	@media (max-width: map-get($breakpoints-down, $size)) {
		@content;
	}
}
